import React from 'react';
import s from './Header.module.scss'
import { NavLink } from 'react-router-dom';

const Header = (props) => {
	return <header className={s.header}>
		<div className={s.container}>
			<a className={s.logo} href=''><img src="https://st2.depositphotos.com/1010751/6667/v/600/depositphotos_66673689-stock-illustration-four-heart-love-people-knot.jpg://st2.depositphotos.com/1010751/6667/v/450/depositphotos_66673689-stock-illustration-four-heart-love-people-knot.jpg" alt="" /></a>
			<div className={s.login}>
				{props.isAuth ?
					<div>{props.login} <button onClick={props.logout}>log out</button></div> :
					<NavLink to="/login">login</NavLink>}

			</div>
		</div>
	</header>
}

export default Header;