import React from 'react';
import './App.css';
import HeaderContainer from './components/Header/HeaderContainer';
import SideBar from './components/SideBar/SideBar';
import { HashRouter, BrowserRouter, Route, Routes } from 'react-router-dom';
import News from './components/News/News';
import Music from './components/Music/Music';
import Settings from './components/Settings/Settings';
import Friends from './components/Friends/Friends';
// import DialogsContainer from './components/Dialogs/DialogsContainer'
import UsersConatiner from './components/Users/UsersContainer';
// import ContentContainer from './components/Content/ContentContainer';
import Login from './components/Login/Login';
import { connect, Provider } from 'react-redux';
import { initializeApp } from './Redux/appReducer'
import Preloader from './common/Preloader/Preloader';
import { Suspense } from 'react';
import { compose } from 'redux';
const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'))
const ContentContainer = React.lazy(() => import('./components/Content/ContentContainer'))

class App extends React.Component {
	componentDidMount() {
		this.props.initializeApp()
	}
	render() {
		if (!this.props.initialized) { return <Preloader /> }

		return (
			<BrowserRouter basename={process.env.PUBLIC_URL}>
				<div className='wrapper'>
					<HeaderContainer />
					<div className='items'>
						<SideBar />
						<div className="content">
							<Suspense fallback={<div><Preloader /></div>}>
								<Routes>

									<Route path="/dialogs/*" element={<DialogsContainer
									/>} />
									<Route path="/content/:userId?/*" element={<ContentContainer />}
									/>
									<Route path="/news" element={<News />} />
									<Route path="/music" element={<Music />} />
									<Route path="/settings" element={<Settings />} />
									<Route path="/users" element={<UsersConatiner />} />
									<Route path="/login" element={<Login />} />
									{/* <Route path="/friends" element={<Friends State={props.Store.getState().SideBar} />} /> */}
								</Routes>
							</Suspense>
						</div>
					</div>
				</div>
			</BrowserRouter>
		);
	}
}


const mapStateToProps = (State) => ({
	initialized: State.app.initialized
})

