const ADD_MESSAGE = "ADD-MESSAGE"




let initialState = {

	DialogsData: [{ id: 1, name: "Maksim" },
	{ id: 2, name: "Alisa" },
	{ id: 3, name: "Olesya" },
	{ id: 4, name: "Sasha" },
	{ id: 5, name: "Andrey" },],

	MessagesData: [
		{ id: 1, message: "hello" },
		{ id: 2, message: "Nice to meet you" },
		{ id: 3, message: "Bye bye" },
		{ id: 4, message: "Hi Hi" },
		{ id: 5, message: "I love ps 5" },
	],
}

const DialogsReducer = (State = initialState, action) => {
	switch (action.type) {
		case ADD_MESSAGE:
			let body = action.newMessageBody
			return {
				...State,
				MessagesData: [...State.MessagesData, { id: 2, message: body }]
			}
		default:
			return State
	}
}



export const addMessageActionCreator = (newMessageBody) => {
	return { type: ADD_MESSAGE, newMessageBody }
}






export default DialogsReducer