import React from 'react';
import { connect } from 'react-redux';
import Users from './Users'
import { follow, setCurrentPage, setUsers, setIsFetching, unFollow, setTotalUsersCount, toggleIsFollowingProgress, requestUsers } from "../../Redux/UsersReducer";
import Preloader from '../../common/Preloader/Preloader'
import { compose } from 'redux';
import { getUsers, getPageSize, getTotalUsersCount, getCurrentPage, getIsFetching, getFollowingInProgress, getPortionSize } from './usersSelect'


class UsersContainer extends React.Component {

	componentDidMount() {
		this.props.requestUsers(this.props.currentPage, this.props.pageSize)
	}

	onPageChenged = (pageNumber) => {
		this.props.requestUsers(pageNumber, this.props.pageSize)
		// this.props.setCurrentPage(pageNumber)
		// this.props.setIsFetching(true)
		// usersAPI.getUsers(pageNumber, this.props.pageSize)
		// 	.then(data => {
		// 		this.props.setIsFetching(false)
		// 		this.props.setUsers(data.items)
		// 	})

	}

	render() {

		return <>
			{this.props.isFetching ? <Preloader /> : null}
			<Users
				portionSize={this.props.portionSize}
				totalUsersCount={this.props.totalUsersCount}
				currentPage={this.props.currentPage}
				setCurrentPage={this.props.setCurrentPage}
				setUser={this.props.setUser}
				pageSize={this.props.pageSize}
				onPageChenged={this.onPageChenged}
				Users={this.props.Users}
				follow={this.props.follow}
				unFollow={this.props.unFollow}
				toggleIsFollowingProgress={this.props.toggleIsFollowingProgress}
				followingInProgress={this.props.followingInProgress}
			/>
		</>
	}
}


