import React from "react";
import s from './Users.module.scss'
import Paginator from "../../common/Paginator/Paginator";
import User from "./User";


let Users = ({ currentPage, totalUsersCount, pageSize, onPageChenged, portionSize, Users, ...props }) => {

	return <div>
		<Paginator currentPage={currentPage} totalItemsCount={totalUsersCount} pageSize={pageSize} onPageChenged={onPageChenged} />
		<div>
			{
				Users.map(u => <User
					followingInProgress={props.followingInProgress}
					User={u}
					key={u.id}
					follow={props.follow}
					unFollow={props.unFollow} />
				)}
		</div>
	</div>
}

export default Users