import { contentAPI } from '../API/api'



const ADD_POST = "ADD-POST"
const SET_USERS_PROFILE = "SET_USERS_PROFILE"
const SET_STATUS = "SET_STATUS"
const DELETE_POST = "DELETE_POST"


let initialState = {
	PostsData: [
		{ id: 1, message: "1234567", likes: "likes: 20" },
		{ id: 2, message: "I'am fine, thank you", likes: "likes: 30" },
		{ id: 3, message: "Bye bye", likes: "likes: 56" },
		{ id: 4, message: "Hi Hi", likes: "likes: 13" },
		{ id: 5, message: "I love ps 5", likes: "likes: 28" },
		{ id: 1, message: "Hi, how are you?", likes: "likes: 20" },
		{ id: 2, message: "I'am fine, thank you", likes: "likes: 30" },
		{ id: 3, message: "Bye bye", likes: "likes: 56" },
		{ id: 4, message: "Hi Hi", likes: "likes: 13" },
		{ id: 5, message: "I love ps 5", likes: "likes: 28" },
	],
	profile: null,
	status: ""
}

const ContentReducer = (State = initialState, action) => {
	switch (action.type) {
		case ADD_POST:
			let newPost = {
				id: "6",
				message: action.newPostBody,
				likes: "1"
			}
			return {
				...State,
				PostsData: [...State.PostsData, newPost],
				newPostText: "",
			}
		case SET_USERS_PROFILE: {
			return { ...State, profile: action.profile }
		}
		case SET_STATUS: {
			return { ...State, status: action.status }
		}
		case DELETE_POST: {
			return { ...State, PostsData: State.PostsData.filter(p => p.id !== action.postId) }
		}
		default:
			return State
	}
}

export const addPostActionCreator = (newPostBody) => {
	return { type: ADD_POST, newPostBody }
}
export const setUsersProfile = (profile) => {
	return { type: SET_USERS_PROFILE, profile }
}
export const setStatus = (status) => {
	return { type: SET_STATUS, status }
}

export const deletePost = (postId) => {
	return { type: DELETE_POST, postId }
}




export const getProfile = (userId) => {
	return async (dispatch) => {
		let data = await contentAPI.getContent(userId)

		dispatch(setUsersProfile(data))
	}
}

export const getStatus = (userId) => {
	return async (dispatch) => {
		let response = await contentAPI.getStatus(userId)
		dispatch(setStatus(response.data))
	}
}

export const updateStatus = (status) => {
	return async (dispatch) => {
		let response = await contentAPI.updateStatus(status)
		if (response.data.resultCode === 0)
			dispatch(setStatus(status))
	}
}




export default ContentReducer