import { authAPI } from '../API/api';
import { stopSubmit } from 'redux-form'

const SET_USERS_DATA = "auth/SET_USERS_DATA"

let initialState = {
	id: null,
	email: null,
	login: null,
	isAuth: false,
}

const AuthReducer = (State = initialState, action) => {
	switch (action.type) {
		case SET_USERS_DATA:
			return {
				...State,
				...action.payload
			}
		default:
			return State

	}
}

export const setAuthUsersData = (id, email, login, isAuth) => {

	return { type: SET_USERS_DATA, payload: { id, email, login, isAuth } }
}

export const auth = () => async (dispatch) => {
	let response = await authAPI.setAuth()

	if (response.data.resultCode === 0) {
		let { id, login, email } = response.data.data
		dispatch(setAuthUsersData(id, email, login, true))
	}
}



export const login = (email, password, rememberMe) =>
	async (dispatch) => {
		let response = await authAPI.login(email, password, rememberMe)
		if (response.data.resultCode === 0) {
			dispatch(auth())
		} else {
			let message = response.data.messages.length > 0 ? response.data.messages[0] : 'error'
			dispatch(stopSubmit('login', { _error: message }))
		}
	}

export const logout = () =>
	async (dispatch) => {
		let response = await authAPI.logout()
		if (response.data.resultCode === 0) {
			dispatch(setAuthUsersData(null, null, null, false))
		}
	}





export default AuthReducer