import React from "react";
import s from './Login.module.scss'
import { Field, reduxForm } from "redux-form";
import { Input } from "../../common/FormsControls/FormsControls";
import { required, maxLengthCreator } from '../../utilits/validators/validators';
import { connect } from "react-redux";
import { login } from '../../Redux/AuthReducer';
import { Navigate } from "react-router-dom";


const LoginForm = (props) => {
	return <div>
		<div>golovin.10174@gmail.com</div>
		<div>Shareman-29055</div>
		<form onSubmit={props.handleSubmit}>
			<div><Field placeholder="email" name={"email"} component={Input} validate={[required, maxLengthCreator(30)]} /></div>
			<div><Field type="password" placeholder="password" name={"password"} component={Input} validate={[required, maxLengthCreator(30)]} /></div>
			<div><Field type="checkbox" component={"input"} name={"rememberMe"} />remember me</div>
			{props.error && <div className={s.errorForm}>{props.error}</div>}
			<div><button>log in</button></div>
		</form>
	</div>
}


const LoginReduxForm = reduxForm({ form: 'login' })(LoginForm)



const Login = (props) => {
	const onSubmit = (formData) => {
		props.login(formData.email, formData.password, formData.rememberMe);
	}

	if (props.isAuth) {
		return <Navigate to="/content" />
	}

	return <div className={s.login}>
		<div>Login</div>
		<LoginReduxForm onSubmit={onSubmit} />
	</div>
}

const mapStateToProps = (State) => ({
	isAuth: State.Auth.isAuth
})

export default connect(mapStateToProps, { login })(Login)
